<template>
		<button v-if="isShow" id="butConnect" class="next buttonX" style="margin-top: 20px;" @click="clickConnect()">Connect</button>
</template>
<!-- sensor type
1: uv
3: colour
4: conductivity
6: VOC
7: humidity -->
<script>
  	let reader;
	export default {
		data() {
			return {
        		port: null,
        		inputDone: null,
        		isShow: true,
        		nData0: 0,
        		nData1: 0,
        		nData2: 0,
        		nData3: 0,
			}
		},
		created() {
			if (sessionStorage.connection == 'true') {
				this.readLoop()
				.catch((e) => {
					console.log(e)
  					this.$store.dispatch('setConnectionData', false)
  				});
			}
		},
		methods: {
			async connect() {
  				this.port = await navigator.serial.requestPort({filters: [{usbVendorId: 0x04D8}]});
  				await this.port.open({ baudrate: 230400 });
  				let decoder = new TextDecoderStream();
          		reader = this.port.readable.getReader();
  				this.readLoop()
  				.catch((e) => {
  					console.log(e)
  					this.$store.dispatch('setConnectionData', false)
  				});
			},
			async disconnect() {
				if (reader) {
					await reader.cancel();
					reader = null;
				}
				await this.port.close();
				this.port = null;
			},
			async clickConnect() {
				if (this.port) {
					await this.disconnect();
					this.toggleUIConnected(false);
					return;
				}
  				await this.connect();
  				this.toggleUIConnected(true);
			},
			async readLoop() {
				let count = 0;
  				while (true) {
    				const { value, done } = await reader.read();
    				this.$emit('nsensor_connected', true);
    				if (value.length == 26) {
    					// console.log(value[2])
    					this.$store.dispatch('setSensortypeData', value[2])
						switch (value[2]) {
							case 1:
								this.decodeUV(value)
								break
							case 3:
								this.decodeColour(value)
								break
							case 4:
								this.decodeConductivity(value)
								break
							case 6:
								this.decodeVOC(value)
								break
							case 7:
								this.decodeHumidity(value)
								break
						}
						this.isShow = false;
						this.$emit('nData0', this.nData0);
						this.$emit('nData1', this.nData1);
						this.$emit('nData2', this.nData2);
						this.$emit('nData3', this.nData3);
						this.$emit('ncount', this.count);
					}
					this.$store.dispatch('setConnectionData', true)
					if (done) {
						console.log("[readLoop] DONE", done);
						reader.releaseLock();
						break;
					}
				}
			},
			decodeUV(value) {
				//data0-Lux data1-UV
				let data0_a = value[9].toString(16)
				let data0_b = value[8].toString(16)
				let data0_c = value[7].toString(16)
				let data0_d = value[6].toString(16)
				let data1_a = value[13].toString(16)
				let data1_b = value[12].toString(16)
				let data1_c = value[11].toString(16)
				let data1_d = value[10].toString(16)
				if (data0_b.length < 2) { data0_b = '0' + data0_b }
				if (data0_c.length < 2) { data0_c = '0' + data0_c }
				if (data0_d.length < 2) { data0_d = '0' + data0_d }
				if (data1_b.length < 2) { data1_b = '0' + data1_b }
				if (data1_c.length < 2) { data1_c = '0' + data1_c }
				if (data1_d.length < 2) { data1_d = '0' + data1_d }
				let data0 = '0x' + data0_a + data0_b + data0_c + data0_d
				let data1 = '0x' + data1_a + data1_b + data1_c + data1_d
				data0 = this.hex2float(data0)
				data1 = this.hex2float(data1)
				this.nData0 = data0.toFixed()
				this.nData1 = data1.toFixed(1)
			},
			decodeColour(value) {
				//data0-red data1-green data2-blue data3-white
				let data0_a = value[7].toString(16)
    			let data0_b = value[6].toString(16)
    			let data1_a = value[9].toString(16)
    			let data1_b = value[8].toString(16)
    			let data2_a = value[11].toString(16)
    			let data2_b = value[10].toString(16)
    			let data3_a = value[13].toString(16)
    			let data3_b = value[12].toString(16)
    			if (data0_b.length < 2) { data0_b = '0' + data0_b }
    			if (data1_b.length < 2) { data1_b = '0' + data1_b }
    			if (data2_b.length < 2) {
    				data2_b = '0' + data2_b
    			}
    			if (data3_b.length < 2) {
    				data3_b = '0' + data3_b
    			}
    			let data0 ='0x' + data0_a + data0_b
    			let data1 ='0x' + data1_a + data1_b
    			let data2 ='0x' + data2_a + data2_b
    			let data3 ='0x' + data3_a + data3_b
    			let color_array = new Uint16Array([data0, data1, data2, data3])
            	color_array = new Uint8Array(color_array)
    			const r = parseInt(color_array[0])
    			const g = parseInt(color_array[1])
    			const b = parseInt(color_array[2])
    			this.rgb2hsv(r, g, b)
			},
			decodeConductivity(value) {
				//data0-conductivity
				let data0_a = value[7].toString(16)
    			let data0_b = value[6].toString(16)
    			let data1_a = value[9].toString(16)
    			let data1_b = value[8].toString(16)
    			if (data0_b.length < 2) {
    				data0_b = '0' + data0_b
    			}
    			if (data1_b.length < 2) {
    				data1_b = '0' + data1_b
    			}
    			let data0 ='0x' + data0_a + data0_b
    			let data1 ='0x' + data1_a + data1_b
    			this.nData0 = parseInt(data0) * parseInt(data1)
			},
			decodeVOC(value) {
				//data0-VOC data1-CO2
				let data0_a = value[7].toString(16)
    			let data0_b = value[6].toString(16)
    			let data1_a = value[9].toString(16)
    			let data1_b = value[8].toString(16)
    			if (data0_b.length < 2) {
    				data0_b = '0' + data0_b
    			}
    			if (data1_b.length < 2) {
    				data1_b = '0' + data1_b
    			}
    			let data0 ='0x' + data0_a + data0_b
    			let data1 ='0x' + data1_a + data1_b
    			let uint_array = new Uint16Array([data0, data1])
            	uint_array = new Uint8Array(uint_array)
    			this.nData0 = parseInt(uint_array[0])
    			this.nData1 = parseInt(uint_array[1])
			},
			decodeHumidity(value) {
				let temp_a = value[7].toString(16)
    			let temp_b = value[6].toString(16)
   				let hum_a = value[9].toString(16)
    			let hum_b = value[8].toString(16)
    			if (temp_b.length < 2) {
    				temp_b = '0' + temp_b
    			}
    			if (hum_b.length < 2) {
    				hum_b = '0' + hum_b
    			}
    			let temp ='0x' + temp_a + temp_b
    			let hum ='0x' + hum_a + hum_b
    			this.nData0 = (parseInt(temp)/100).toFixed()
    			this.nData1 = (parseInt(hum)/100).toFixed()
			},
			hex2float(raw) {
				const s = (raw&0x80000000)?-1:1
    			const e = ((raw>>23)&0xff)-127
    			const c = 1+((raw&0x7fffff)/0x7fffff)
    			return  s*c*Math.pow(2,e)
			},
			rgb2hsv (r, g, b) {
    			let rabs, gabs, babs, rr, gg, bb, h, s, v, diff, diffc, percentRoundFn;
    			rabs = r / 255;
    			gabs = g / 255;
    			babs = b / 255;
    			v = Math.max(rabs, gabs, babs),
    			diff = v - Math.min(rabs, gabs, babs);
    			diffc = c => (v - c) / 6 / diff + 1 / 2;
    			percentRoundFn = num => Math.round(num * 100) / 100;
    			if (diff == 0) {
    			    h = s = 0;
    			} else {
     			   	s = diff / v;
    			    rr = diffc(rabs);
   				    gg = diffc(gabs);
    			    bb = diffc(babs);
   		 		    if (rabs === v) {
   				        h = bb - gg;
    			    } else if (gabs === v) {
    			        h = (1 / 3) + rr - bb;
    			    } else if (babs === v) {
    			        h = (2 / 3) + gg - rr;
    			    }
    			    if (h < 0) {
    			        h += 1;
    			    }else if (h > 1) {
    			        h -= 1;
    			    }
    			}
        		this.nData0 = Math.round(h * 360),
        		this.nData1 = percentRoundFn(s * 100),
        		this.nData2 = percentRoundFn(v * 100)
			},
			toggleUIConnected(connected) {
				const butConnect = document.getElementById("butConnect");
  				let lbl = "Connect";
  				if (connected) {
    				lbl = "Disconnect";
  				}
  				butConnect.display = null;
			},
			buf2hex(buffer) {
				return Array.prototype.map.call(new Uint8Array(buffer), x => ('00' + x.toString(16)).slice(-2)).join('');
			}
		}
	}
</script>

<style scoped>
button {
	width: 144px;
      height: 40px;
      border-radius: 5px;
      font-size: 18px;
      font-weight: 600;
      color: white;
      background-color: #FA5708;
      border: 2px solid #FDB18B;
      cursor: pointer;
}
</style>