          outlined
          clearable
          placeholder="Search inquiries"
          prepend-inner-icon="mdi-magnify"
          v-model="searchQuarry"
        ></v-text-field>
      </v-col>
      <v-col justify="end">
        <v-btn-toggle style="height: 100%; vertical-align: top;">
          <v-btn class="ml-3" tile style="height: 75% !important; vertical-align: top;" @click="filterBySensor('')">
            All
          </v-btn>
          <v-btn class="ml-3" tile style="height: 75%; vertical-align: top;" @click="filterBySensor('uv')">
            <v-avatar class="ma-3">
