                :src="require('@/assets/sensor-voc@2x.png')"
                alt="John"
              ></v-img>
            </v-avatar>
          </v-btn>
          <v-btn class="ml-3" tile style="height: 75%; vertical-align: top;" @click="filterBySensor('tem')">
