                :src="require('@/assets/sensor-humidity@2x.png')"
                alt="John"
              ></v-img>
            </v-avatar>
          </v-btn>
          <v-btn class="ml-3" tile style="height: 75%; vertical-align: top;" @click="filterBySensor('col')">
            <v-avatar class="ma-3">
              <v-img
                :src="require('@/assets/sensor-colour@2x.png')"
                alt="John"
              ></v-img>
            </v-avatar>
          </v-btn>
        </v-btn-toggle>
      </v-col>
    </v-row>   
    <v-container class="my-5" v-if="!searchQuarry">
      <v-layout row wrap>
        <v-flex xs12 sm6 md4 lg3 v-for="finding in inquiries" v-bind:key="finding.Id" @click="toFinding(finding.Id)">
          <v-card class="text-xs-left ma-3" style="text-align: left !important;" link>
            <v-img :src="'https://kiwrious.org/Storage/'+ getMedia(finding)" aspect-ratio="1.4"
              lazy-src="https://kiwrious.org/Storage/System/default-placeholder.jpg"
            ></v-img>
            <v-chip
              @click.stop="searchQuarry=finding.UserNickname"
              class="ma-2"
              color="#FFFFFF"
              text-color="black"
            >
              <v-avatar left style="margin-right: 5px;">
                <v-icon>mdi-account-circle</v-icon>
              </v-avatar>
              <div>
              {{finding.UserNickname}}
              </div>
            </v-chip>
            <v-card-title style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{finding.Name}}</v-card-title>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
    <v-container class="my-5" v-else>
      <v-layout row wrap>
        <v-flex xs12 sm6 md4 lg3 v-for="finding in filtered" v-bind:key="finding.Id" @click="toFinding(finding.Id)">
          <v-card class="text-xs-left ma-3" style="text-align: left !important;" link>
            <v-img :src="'https://kiwrious.org/Storage/'+ getMedia(finding)" aspect-ratio="1.4"
            lazy-src="https://kiwrious.org/Storage/System/default-placeholder.jpg">
            </v-img>
            <v-chip
              class="ma-2"
              color="#FFFFFF"
              text-color="black"
            >
              <v-avatar left style="margin-right: 5px;">
                <v-icon>mdi-account-circle</v-icon>
              </v-avatar>
              <div>
              {{finding.UserNickname}}
              </div>
            </v-chip>
            <v-card-title style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{finding.Name}}</v-card-title>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-container>
</template>

<script>
	import axios from 'axios'

  var storage = [];
  const api = axios.create({
    baseURL: process.env.VUE_APP_URL,
  })
  export default {
    data() {
      return {
        inquiries: null,
        searchQuarry: "",
        filtered: null,
        allInquiries: null,
        cards: [
        { title: 'Pre-fab homes', src: 'https://cdn.vuetifyjs.com/images/cards/house.jpg', flex: 12 },
        { title: 'Favorite road trips', src: 'https://cdn.vuetifyjs.com/images/cards/road.jpg', flex: 6 },
        { title: 'Best airlines', src: 'https://cdn.vuetifyjs.com/images/cards/plane.jpg', flex: 6 },
      ],
      }
    },
    beforeRouteEnter(to, from, next) {
      var organizationId = sessionStorage.oid;
      const url = '/finding/organization/'+ organizationId;//948a06f5-5f03-498a-9258-3f89ea1e2362'
      api.get(url)
      .then((response) => {
       // this.$loading.hide();
        storage = [];
        response.data.forEach(f=>{
          if(f.Type==1){
            storage.push(f);
          }
        });
        next()
      })
      .catch((error) => {
        this.$loading.hide();
        console.log(error)
        this.$message({
          message: 'Network Error, Please try again',
          type: 'error'
        })
      })
    },
    created() {
      this.$loading.show({delay:0, background: '#FFFFFF'});  
      this.inquiries = storage;
      this.allInquiries = storage;
    },
    methods: {
      toFinding(fid) {
        this.$store.dispatch('setFindingIdData', fid)
        this.$router.push({path: '/finding'})
      },
      getMedia(finding){
        if(finding.Dataset[0].Media == null){
          return "default-finding-image.jpg";
        }
        return finding.Dataset[0].Media+'.jpg';
      },
      filterResults(){
        if(this.searchQuarry==null){
          this.inquiries = this.allInquiries;
          return;
        }
        this.filtered = this.inquiries.filter(finding=>finding.Name.toLowerCase().includes(this.searchQuarry.toLowerCase()) || finding.UserNickname.toLowerCase().includes(this.searchQuarry.toLowerCase()));
      },
      filterBySensor(sensorName){
          if(!sensorName){
              this.inquiries = this.allInquiries;
              return;
          }
          this.inquiries = this.allInquiries.filter(c=>c.SensorId.includes(sensorName));
      }
    },
    watch: {
       inquiries(){
         this.$loading.hide();
       },
       searchQuarry(){
         this.filterResults();
       }
    }
  }
</script>

